	module.exports = {"data":{"post":{"html":"<p>We've been looking into Kindergarten the past couple of weeks, which we want Christopher to start this fall. We have a full-day slot reserved in the regular group, but given his speech development it has been strongly recommended that he attend a so-called 'integration' group, where the group is smaller and there are regular speech therapy sessions. Mama and Christopher were there on Tuesday, and they both loved it. We'd also most likely be able to have a doctor certify that Christopher needs the integration group, so that the county would cover all costs.</p>\n<p>Here's the problem. The integration group is only half-day, afternoons, 12:30-17:30, here in Jesteburg. We both work full-time in Hamburg, an hours' commute away. Do the math. Our Tagesmutter is in Buchholz, 15 minutes away, so arranging transport from there is not feasible.</p>\n<p>In fact, right now if you were to ask whether my job is worth making all the arrangements for Christopher to attend this Kindergarten while I work 8 hours in Hamburg, I would truthfully have to say no.</p>","fields":{"title":"Crossroads","date":"08 Apr 2004","url":"https://papascott.de/2004-04-08-crossroads/"}}},"pathContext":{"slug":"/2004-04-08-crossroads/","prev":{"fields":{"title":"Samba 3... yes!","date":"07 Apr 2004","slug":"/2004-04-07-samba-3-yes/"}},"next":{"fields":{"title":"Good News for the Chancellor","date":"08 Apr 2004","slug":"/2004-04-08-good-news-for-the-chancellor/"}}}}

